.header {
  display: flex;
  justify-content: space-between;
  margin: 10px;
  border: 1px solid black;
}
.logo {
  width: 100px;
}

.nav-items{
  padding: 0px 20px;
}

.nav-items > ul {
  font-size: 24px;
  display: flex;
  list-style-type: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin:10px;
}

.res-container{
  display: flex;
  flex-wrap: wrap;
}

.res-card {
  margin: auto;
  padding:10px;
  width:200px;
  height:400px;
}

.res-card:hover{
  cursor: pointer;
  border: 1px solid black;
}

.search{
  padding: 10px;
}

.res-logo{
  width: 100%;
}

.filter-btn{
  margin:10px;
  cursor: pointer;
}
